@use 'hero';
@use 'event';

body{
    background-color: black;
    font-family: Roboto, sans-serif;
    color: #fff;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media (max-width: 640px){
        max-width: 80%;
    }
    
    @media (max-width: 1024px){
        max-width: 90%;
    }
}

